@tailwind base;
@tailwind components;
@tailwind utilities;

/* Ensure the app can be full-bleed and remove default browser margins */
html, body, #root {
	height: 100%;
	margin: 0;
	padding: 0;
	background-color: #000000; /* page background so edges are black */
	box-sizing: border-box;
}

/* optional: make box-sizing predictable */
*, *::before, *::after { box-sizing: inherit; }

/* Color system for the app (tweak these to change the whole theme) */
:root {
	--bg-color: #060606; /* very dark base */
	--bg-gradient: linear-gradient(180deg, #050505 0%, #070707 40%, #0b0b0b 100%);
	--section-bg: linear-gradient(180deg, rgba(7,7,7,0.8), rgba(12,12,12,0.9));
	--section-alt-bg: #030712; /* used for lore section */
	--card-bg: #0f1112; /* card background */
	--card-border: #1f2937; /* subtle card border */
	--text-color: #e5e7eb; /* main body text */
	--heading-color: #a1a1aa; /* large heading */
	--muted: #6b7280; /* muted labels */
	--muted-light: #9ca3af; /* secondary muted text */
	--accent: #ffffff; /* bright text for values */
	--logo-border: #374151; /* logo ring */
	--primary-pill-bg: #ffffff;
	--primary-pill-text: #000000;
	--pill-ghost-bg: #1f2937;
	--pill-ghost-text: #e5e7eb;
	--contract-bg: #1c1c1c;
	--border-color: #2a2a2a;
}

/* Make all links inherit the theme color and remove browser default purple/blue */
a, a:visited {
	color: inherit;
	text-decoration: none;
}

/* Preserve keyboard accessibility but avoid strong default outlines */
a:focus {
	outline: 2px solid rgba(255,255,255,0.08);
	outline-offset: 2px;
}
